<script>
    import KEY from '../common/key';
    import { writable } from '../common/storage';

    const closed = writable(KEY.SERIES_WARNING_CLOSED, false);

    function onClose() {
        closed.set(true);
    }
</script>

<div class="notice {$closed ? 'closed' : ''}">
    <div>시리즈는 로컬저장소를 사용합니다. 브라우저 데이터를 삭제하면 저장된 정보가 모두 사라지니 주의하세요.
        <!-- svelte-ignore a11y-missing-attribute -->
        <a class="close" on:click={onClose}>다시 보지 않기.</a>
    </div>
</div>

<style lang="scss">
    $width: 768px;

    .notice {
        height: 50px;
        background-color: #efefef;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: solid 1px #ddd;
        font-size: .75rem;
        padding: .2rem 8%;

        &.closed {
            display: none;
        }

        .close {
            margin-left: .25rem;
            cursor: pointer;
        }
    }

    @media (max-width: 480px) {
        .notice {
            padding: .25rem 4%;
