<script>
    export let params = {};

    import { onMount, onDestroy } from 'svelte';

    import { status } from './series';
    import { getSeriesByTitle } from './store/series';
    import { HistoryFn } from './SeriesFunctions';

    import NotFound from '../NotFound.svelte';
    import Layout from '../Layout.svelte';
    import PlayButton from './component/PlayButton.svelte';
    import BookmarkButton from './component/BookmarkButton.svelte';
    import Date from './component/Date.svelte';
    import Requires from './component/Requires.svelte';
    import Recommends from './component/Recommends.svelte';
    import Warn from './component/Warn.svelte';
    import Contents from './component/Contents.svelte';

    let innerWidth;
    let elNavigation;
    let observer;
    let item;
    let postTitle;

    onMount(() => {
        elNavigation = document.querySelector('nav');

        onWindowSizeChanged();

        observer = new ResizeObserver(onWindowSizeChanged);
        observer.observe(elNavigation);

        postTitle = HistoryFn.getLatestPostBySeriesTitle(item.title);
    });

    onDestroy(() => {
        observer.unobserve(elNavigation);
        observer.disconnect();
    });

    $: item = getSeriesByTitle(decodeURI(params.groups.title).replaceAll('/', ''));

    function onWindowSizeChanged() {
        if (innerWidth > 768) {
            elNavigation.style.position = 'fixed';
        } else {
            elNavigation.style.position = 'relative';
        }
    }
</script>

<svelte:window bind:innerWidth={innerWidth} />

{#if item === undefined}
    <NotFound />
{:else}
    <Layout>
        <div class="detail">
            <div class="col col-1">
                <div class="row row-1">
                    <div class="thumbnail">
                        {#if item.thumbnail !== undefined && item.thumbnail !== ""}
                            <img src="{item.thumbnail}" alt="thumbnail">
                        {:else}
                            <svg width="100%" height="100%">
                                <rect fill="#f3f3f3" x="0" y="0" width="100%" height="100%" />
                                <text fill="rgba(0,0,0,0.75)" font-family="Lobster" font-size="2rem" x="50%" y="46%" text-anchor="middle" cursor="default">Show Yourself</text>
                                <text fill="rgba(0,0,0,0.5)" font-family="Roboto" font-size="1.25rem" x="50%" y="58%" text-anchor="middle" cursor="default">오리지널</text>
                                <text fill="rgba(0,0,0,0.5)" font-family="Roboto" font-size="1.25rem" x="50%" y="65%" text-anchor="middle" cursor="default">시리즈</text>
                            </svg>
                        {/if}
                    </div>
                    <div class="btn-wrap">
                            <BookmarkButton title={item.title}/>
                            {#if item.status !== status.UPCOMING}
                                <PlayButton series={item} post={postTitle} />
                            {/if}
                        <div class="date">
                            <Date item={item} />
                        </div>
                    </div>
                </div>
                <div class="row row-2">
                    <div class="title">
                        <div class="t">{item.title}</div>
                    </div>
                    <div class="description">
                        {@html item.description}
                    </div>
                    <div class="requires">
                        <Requires item={item} />
                    </div>
                    <div class="recommends">
                        <Recommends item={item} />
                    </div>
                    <div class="warn">
                        <Warn item={item} />
                    </div>
                    <div class="contents">
                        <Contents item={item} />
                    </div>
                </div>
            </div>
        </div>
    </Layout>
{/if}

