<script>
    export let params = {};

    import { titleToUrl } from '../../common/utils';
    import { getSeriesByTitle } from './store/series';

    import Layout from '../Layout.svelte';
    import NotFound from '../NotFound.svelte';
    import InternalServerError from '../InternalServerError.svelte';

    $: title = decodeURI(params.groups.title);
    $: subtitle = decodeURI(params.groups.subtitle);
    $: series = getSeriesByTitle(decodeURI(params.groups.title));

    $: current = series.contents.filter(content => {
            return titleToUrl(content.title) === subtitle;
        })[0];
</script>

{#if current !== undefined}
    {#await current.component}
        <Layout center={true}>
            포스트 불러오는 중...
        </Layout>
    {:then component}
        {#if component !== undefined}
            <svelte:component this={component.default} series={series} title={title} subtitle={subtitle}>
            </svelte:component>
        {:else}
            <InternalServerError />
        {/if}
    {:catch e}
        <InternalServerError />
    {/await}
{:else}
    <NotFound />
{/if}
