<script>
    export let params = {};

    import { getPostByTitle } from './store/article';
    import Layout from '../Layout.svelte';
    import NotFound from '../NotFound.svelte';
    import InternalServerError from '../InternalServerError.svelte';

    $: currentPage = decodeURI(params.groups.title);
    $: post = getPostByTitle(currentPage);
</script>

{#if post !== undefined}
    {#await post.component}
        <Layout center={true}>
            포스트 불러오는 중...
        </Layout>
    {:then component}
        {#if component !== undefined}
            <svelte:component this={component.default} post={post}>
            </svelte:component>
        {:else}
            <InternalServerError />
        {/if}
    {:catch e}
        <div>{e}</div>
        <InternalServerError />
    {/await}
{:else}
    <NotFound />
{/if}
