<script>
    export let params = {};

    import { onMount } from 'svelte';
    import Layout from '../Layout.svelte';
    import Tag from './components/Tag.svelte';
    import List from './components/List.svelte';
    import Paging from './components/Paging.svelte';
    import { totalPageCount, setCurrentPage } from './store/article';
    import { push } from 'svelte-spa-router';

    $: currentPage = (params.groups !== undefined && params.groups.page !== undefined) ? params.groups.page : 1;

    onMount(() => {
        if (currentPage > $totalPageCount) {
            push('/article');
            return;
        }
        setCurrentPage(currentPage);
    });

    function onHashChange(e) {
        if (currentPage > $totalPageCount) {
            push('/article/1');
            return;
        }
        setCurrentPage(currentPage);
    }
</script>

<svelte:window on:hashchange={onHashChange} />

<!-- TODO: 각 하위 컴포넌트에 post를 attr로 전달 하는 방식으로 변경. (하위 컴포넌트에서 store 접근 금지.) -->
<Layout>
    <div class="article">
        <div class="col-1">
            <Tag />
        </div>
        <div class="col-2">
            <List />
        </div>
        <div class="col-3">
            <Paging current={currentPage} total={$totalPageCount} />
        </div>
    </div>
</Layout>

