<script>
    export let category;
    export let items;

    import { RecentFn } from '../SeriesFunctions';
    import { category as _category } from '../series'
    import List from './List.svelte';

    let wrap;
    let isSticky = false;

    function onScroll() {
        if (wrap !== undefined && wrap !== null) {
            let wrapTop = wrap.offsetTop;
            let windowY = window.scrollY;

            if (windowY > wrapTop) {
                isSticky = true;
            } else {
                isSticky = false;
            }
        }
    }
</script>

<svelte:window on:scroll={onScroll} />

{#if items.length !== 0}
    <div bind:this={wrap}>
        <div class="category {(isSticky) ? 'sticky': ''}">
            <div>{category}</div>
            {#if category === _category.INPROGRESS}
                <div class="d" on:click={() => RecentFn.resetRecents()}>기록 지우기</div>
            {/if}
        </div>
        <List items={items} category={category} />
    </div>
{/if}

<style lang="scss">
    $width: 768px;

    .category {
        position: sticky;
        position: -webkit-sticky;
        top: -1px;
        font-weight: 700;
        padding: 0 8%;
        font-size: 1.25rem;
        vertical-align: middle;
        background-color: rgba(255, 255, 255, .8);
        backdrop-filter: blur(4px);
        -webkit-backdrop-filter: blur(4px);
        z-index: 999;
        height: 4rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        transition: all .2s ease;

        .d {
            font-size: .8rem;
            font-weight: 400;
            margin-left: 1rem;
            padding: .5rem;
            border-radius: 5px;
            cursor: pointer;
            display: none;

            &:hover {
                background-color: #efefef;
                text-decoration: underline;
            }
        }

        &.sticky {
            border-bottom: solid 1px #ddd;

            .d {
                display: block;
            }
        }
    }

    @media (max-width: $width) {
        .category {
            padding: 0 4%;
            height: 4rem;
            background-color: #fff;
            backdrop-filter: none;
            -webkit-backdrop-filter: none;
        }
    }
</style>
