import KEY from '../../common/key';
import { get } from 'svelte/store';
import { writable } from '../../common/storage';
import _get from 'lodash/get';
import _merge from 'lodash/merge';
import _findKey from 'lodash/findKey';

const maxRecentSize = 4;

const bookmarkStore = writable(KEY.SERIES_BOOKMARKS, {});
const fullscreenStore = writable(KEY.SERIES_POST_FULL_SCREEN, false);
const historyStore = writable(KEY.SERIES_HISTORY, {});
const recentStore = writable(KEY.SERIES_RECENT, []);

export const BookmarkFn = {
    add(title) {
        let bookmark;

        if (!get(bookmarkStore)) {
            bookmark = {};
        } else {
            bookmark = get(bookmarkStore);
        }

        bookmark[title] = true;
        bookmarkStore.set(bookmark);
    },
    remove(title) {
        if (get(bookmarkStore) && get(bookmarkStore)[title]) {
            let bookmark = get(bookmarkStore);
            delete bookmark[title];
            bookmarkStore.set(bookmark);
        }
    },
    get() {
        return get(bookmarkStore);
    },
    store: bookmarkStore
};

export const FullscreenFn = {
    set(value) {
        fullscreenStore.set(value);
    },
    store: fullscreenStore
};

export const HistoryFn = {
    getLatestPostBySeriesTitle(seriesTitle) {
        const history = get(historyStore);
        return _get(history, `${seriesTitle}`, {})['latest'];
    },
    setLatestPost(seriesTitle, postTitle) {
        const updated = _merge({}, get(historyStore), {
            [seriesTitle]: {
                "latest": postTitle
            }
        });
        historyStore.set(updated);
    },
    setPostToRead(seriesTitle, postTitle) {
        const updated = _merge({}, get(historyStore), {
            [seriesTitle]: {
                read: {
                    [postTitle]: true
                }
            }
        });
        historyStore.set(updated);
    },
    isReadPost(seriesTitle, postTitle) {
        const history = get(historyStore);
        const value = _get(history, `${seriesTitle}`, {});
        return _get(value.read, `${postTitle}`, false);
    },
    getPostIndex(contents, postTitle) {
        return parseInt(_findKey(contents, ['title', postTitle]));
    },
    store: historyStore
};

export const RecentFn = {
    addRecent(seriesTitle) {
        let recents = get(recentStore);
        if (recents.includes(seriesTitle)) {
            const idx = recents.indexOf(seriesTitle);
            if (idx > -1) {
                recents.splice(idx, 1);
            }
            recents.unshift(seriesTitle);
        } else {
            if (recents.length < maxRecentSize) {
                recents.unshift(seriesTitle);
            } else {
                recents.pop();
                recents.unshift(seriesTitle);
            }
        }
        recentStore.set(recents);
    },
    getRecents() {
        return get(recentStore);
    },
    resetRecents() {
        recentStore.set([]);
    },
    store: recentStore
};
