<script>
    export let items;
    export let category;

    import * as series from '../series.js';
    import { titleToUrl } from '../../../common/utils.js';

    import ListItem from './ListItem.svelte';
    import InprogressItem from './InProgressListItem.svelte';
    import FavoriteListItem from './FavoriteListItem.svelte';
    import UpcomingListItem from './UpComingListItem.svelte';
</script>

<div class="content-wrap">
    <ul>
        {#each items as item}
            <li class="series-list-item">
                <a href="/#/series/{titleToUrl(item.title)}">
                    {#if category === series.category.ORIGINAL}
                        <ListItem item={item} />
                    {:else if category === series.category.INPROGRESS}
                        <InprogressItem item={item} />
                    {:else if category === series.category.FAVORITE}
                        <FavoriteListItem item={item} />
                    {:else}
                        <UpcomingListItem item={item} />
                    {/if}
                </a>
            </li>
        {/each}
    </ul>
</div>

