<script>
    export let current;
    export let total;

    import { push } from 'svelte-spa-router';
    import { isNumber } from '../../../common/utils';

    $: page = current;

    function onEnter(e) {
        if (e.key == 'Enter') {
            if (!isNumber(page)) {
                page = 1;
            }
            push('/article/' + page);
        }
    }
</script>

<div class="paging">
    <div class="nav prev">
        {#if current > 1}
            <a href="/#/article/{(parseInt(current) - 1)}">이전</a>
        {:else}
            <span>이전</span>
        {/if}
    </div>
    <div class="page">
        <span><input type="text" bind:value={page} on:keydown={onEnter}></span>
        <span>/</span>
        <span>{total}</span>
    </div>
    <div class="nav next">
        {#if current < total}
            <a href="/#/article/{(parseInt(current) + 1)}">다음</a>
        {:else}
            <span>다음</span>
        {/if}
    </div>
</div>

