<script>
    export let item;

    import { push } from 'svelte-spa-router';
    import { titleToUrl } from '../../../common/utils';
    import { HistoryFn } from '../SeriesFunctions';

    let readPercentage = 0;
    const latestPost = HistoryFn.getLatestPostBySeriesTitle(item.title);
    const latestPostIndex = HistoryFn.getPostIndex(item.contents, latestPost);

    HistoryFn.store.subscribe((value) => {
        const history = value[item.title];
        if (!history) {
            return;
        }
        const readCount = Object.entries(value[item.title].read).filter(([key, value]) => value).length;
        readPercentage = (readCount / item.contents.length) * 100;
    });

    function onResume() {
        push(`/series/${titleToUrl(item.title)}/${titleToUrl(latestPost)}`);
    }
</script>

<div class="item">
    <div class="thumbnail">
        <div class="wrap">
            {#if item.thumbnail !== "" || item.thumbnail === undefined}
                <img src="{item.thumbnail}" alt="thumbnail">
            {:else}
                <svg width="100%" height="100%">
                    <rect fill="#f3f3f3" x="0" y="0" width="100%" height="100%" />
                    <text class="l" fill="rgba(0,0,0,0.75)" font-family="Lobster" font-size="1rem" x="50%" y="44%" text-anchor="middle" cursor="default">Show Yourself</text>
                    <text class="o" fill="rgba(0,0,0,0.5)" font-family="Roboto" font-size=".9rem" x="50%" y="56%" text-anchor="middle" cursor="default">오리지널</text>
                    <text class="s" fill="rgba(0,0,0,0.5)" font-family="Roboto" font-size=".9rem" x="50%" y="65%" text-anchor="middle" cursor="default">시리즈</text>
                </svg>
            {/if}
        </div>
        <div class="progress"><div class="v" style="width: {readPercentage}%"></div></div>
        <div class="overlay" on:click|preventDefault|stopPropagation={onResume}>이어보기</div>
    </div>
    <div class="description">
        <div class="title">{item.title}</div>
        <div class="desc">{latestPostIndex + 1}. {latestPost}</div>
    </div>
</div>

