import Home from './pages/home/Home.svelte';
import Article from './pages/article/Article.svelte';
import ArticlePost from './pages/article/Post.svelte';
import Series from './pages/series/Series.svelte';
import SeriesDetail from './pages/series/Detail.svelte';
import SeriesPost from './pages/series/Post.svelte';
import Project from './pages/project/Project.svelte';
import Contact from './pages/contact/Contact.svelte';
import NotFound from './pages/NotFound.svelte';

const routes = new Map();

routes.set('/', Home);
routes.set('/article', Article);
routes.set(/^\/article\/(?<page>[0-9]+)/, Article);
routes.set(/^\/article\/(?<title>(.*)+)$/, ArticlePost);
routes.set('/series', Series);
routes.set(/^\/series\/(?<title>[^\/(.*)]+)\/(?<subtitle>[^\/(.*)]+)+/, SeriesPost);
routes.set(/^\/series\/(?<title>(.*)+)(\/?)$/, SeriesDetail);
routes.set('/project', Project);
routes.set('/contact', Contact);
routes.set('*', NotFound);

export default routes;
