import { readable } from 'svelte/store';
import { titleToUrl } from '../../../common/utils';
import * as _series from '../series.js';

export const allSeries = readable(_series.series);

export const originalSeries = readable(
    _series.series.filter(list => list.status !== _series.status.UPCOMING)
);

export const upcomingSeries = readable(
    _series.series.filter(list => list.status === _series.status.UPCOMING)
);

export function getSeriesByTitle(title) {
    let series = _series.series.filter(series => {
        return titleToUrl(series.title) === title;
    });
    return (series !== undefined && series.length > 0) ? series[0] : undefined;
}
