import { writable, derived } from 'svelte/store';
import { titleToUrl } from '../../../common/utils';
import posts from '../posts.js';

Object.filter = (obj, predicate) =>
    Object.keys(obj)
        .filter(key => predicate(key))
        .reduce((res, key) => (res[key] = obj[key], res), {});

function intersection(a, b) {
    return new Set([...a].filter(x => b.has(x)));
}

function hasIntersection(a, b) {
    return intersection(a, b).size != 0;
}

let tags = (() => {
    let obj = {};
    posts.map(post => {
        post.tag.map(tag => {
            if (tag in obj) {
                obj[tag.toUpperCase()]++;
            } else {
                obj[tag.toUpperCase()] = 1;
            }
        });
    });
    return obj;
})();

let _selectedTagList = new Set();
let selectedTagList = writable(_selectedTagList);

let maxPageCount = 10;
export const currentPage = writable(0);

const _articles = derived(
    selectedTagList,
    $selectedTagList => {
        if (_selectedTagList.size == 0) {
            return posts;
        }
        return posts.filter(post => {
            if (hasIntersection([...post.tag.map(tag => tag.toUpperCase())], _selectedTagList)) {
                return post;
            }
        });
    }
);

export const allTags = derived(
    selectedTagList,
    $selectedTagList => {
        return tags;
    }
);

export const selectedTags = derived(
    selectedTagList,
    $selectedTagList => {
        return Object.filter(tags, tag => _selectedTagList.has(tag));
    }
);

export const unSelectedTags = derived(
    selectedTagList,
    $selectedTagList => {
        return Object.filter(tags, tag => !_selectedTagList.has(tag));
    }
);

export const articles = derived(
    [_articles, currentPage],
    ([$$_articles, $$page]) => {
        let start = $$page * maxPageCount;
        let end = start + maxPageCount;
        return $$_articles.slice(start, end);
    }
);

export function getPostByTitle(title) {
    let post = posts.filter(post => titleToUrl(post.title) == title);
    if (post.length !== 0 && post[0].component !== undefined) {
        return post[0];
    } else {
        return undefined
    }
}

export function addSelectedTags(tag) {
    _selectedTagList.add(tag);
    selectedTagList.set(_selectedTagList);
};

export function removeSelectedTags(tag) {
    _selectedTagList.delete(tag);
    selectedTagList.set(_selectedTagList);
}

export function resetSelectedTags() {
    _selectedTagList = new Set();
    selectedTagList.set(_selectedTagList);
}

export const totalPageCount = derived(
    _articles,
    $$_articles => {
        return Math.ceil($$_articles.length / maxPageCount);
    }
);

export function setCurrentPage(page) {
    currentPage.set(page - 1);
}
