<script>
    import { allSeries, originalSeries, upcomingSeries } from './store/series.js';
    import { category } from './series';
    import { BookmarkFn, RecentFn } from './SeriesFunctions';
    import Layout from '../Layout.svelte';
    import ListContainer from './component/ListContainer.svelte';
    import Pannel from '../../components/Pannel.svelte';

    let bookmarks;
    let recents;

    BookmarkFn.store.subscribe((list) => {
        bookmarks = $allSeries.filter((series) => {
            return list.hasOwnProperty(series.title);
        });
    });

    RecentFn.store.subscribe((list) => {
        recents = $allSeries.filter((series) => {
            return list.includes(series.title);
        });
    });
</script>

<Pannel />
<Layout>
    <div class="series">
        <ListContainer items={$originalSeries} category={category.ORIGINAL} />
        <ListContainer items={recents} category={category.INPROGRESS} />
        <ListContainer items={bookmarks} category={category.FAVORITE} />
        <ListContainer items={$upcomingSeries} category={category.UPCOMING} />
    </div>
</Layout>

<style lang="scss">
    .series {
        margin-top: .5rem;
        margin-bottom: 2rem;
    }

    @media (max-width: 587px) {
        .series {
            margin-top: 0;
            margin-bottom: 1rem;
        }
    }
</style>
