<script>
    import { location } from 'svelte-spa-router';
    import Item from './Item.svelte';
    import Github from './Github.svelte';
    import Coffee from './Coffee.svelte';

    let isExpanded = false;
    const maxWidth = 768;
    let windowWidth;
    let currentLocation = $location.split('/')[1].toUpperCase();

    function toggleExpanded() {
        if (windowWidth <= maxWidth) {
            isExpanded = !isExpanded;
        }
    }

    function onWindowResize() {
        if (windowWidth > maxWidth) {
            isExpanded = false;
        }
    }
</script>

<svelte:window bind:innerWidth={windowWidth} on:resize="{onWindowResize}" />

<nav class="{isExpanded ? 'active' : ''}" on:click="{toggleExpanded}">
    <div class="title-container">
        Show Yourself
    </div>
    <div class="nav-container">
        <!-- TODO: 시맨틱 웹에 따라 item 제거 하고, 타이틀을 표시 할 다른 방법으로 구현 필요. -->
        <Item show={(windowWidth <= 768) && !isExpanded}>
            {currentLocation === '' ? 'HOME' : currentLocation}
        </Item>
        <Item href="/">HOME</Item>
        <Item href="/article">ARTICLE</Item>
        <Item href="/series">SERIES</Item>
        <Item href="/project">PROJECT</Item>
    </div>
    <div class="link-container {isExpanded ? 'active' : 'hide'}">
        <div>
            <Github />
        </div>
        <div>
            <Coffee />
        </div>
    </div>
</nav>

