<script>
    export let series;
    export let post;
    import { titleToUrl } from '../../../common/utils';
    import { push } from 'svelte-spa-router';
    import { HistoryFn } from '../SeriesFunctions';
    import Button from '../../../components/Button.svelte';
    import Play from 'svelte-bootstrap-icons/lib/PlayFill/PlayFill.svelte';

    let latestPost = HistoryFn.getLatestPostBySeriesTitle(series.title, post);

    function onResume() {
        push(`/series/${titleToUrl(series.title)}/${titleToUrl(latestPost)}`);
    }

    function onPlay() {
        push(`/series/${titleToUrl(series.title)}/${titleToUrl(series.contents[0].title)}`);
    }
</script>

{#if latestPost}
    <Button icon={Play} size={28} color="#333" onClick={onResume}>이어보기</Button>
{:else}
    <Button icon={Play} size={28} color="#333" onClick={onPlay}>첫 글 보기</Button>
{/if}
