<script>
    export let title;

    import { BookmarkFn } from '../SeriesFunctions';
    import Button from '../../../components/Button.svelte';
    import Bookmark from 'svelte-bootstrap-icons/lib/BookmarkStar/BookmarkStar.svelte';
    import BookmarkFill from 'svelte-bootstrap-icons/lib/BookmarkStarFill/BookmarkStarFill.svelte';

    let store = BookmarkFn.store;
</script>

{#if $store[title]}
    <Button icon={BookmarkFill} size={20} color="#c90505" onClick={() => BookmarkFn.remove(title)}>즐겨찾기에서 제거</Button>
{:else}
    <Button icon={Bookmark} size={20} color="#c90505" onClick={() => BookmarkFn.add(title)}>즐겨찾기에 추가</Button>
{/if}
