<script>
    import Trash from 'svelte-bootstrap-icons/lib/Trash/Trash.svelte';
    import { push } from 'svelte-spa-router';
    import { 
        allTags,
        selectedTags,
        addSelectedTags,
        removeSelectedTags,
        resetSelectedTags
    } from '../store/article';

    function addTags(key) {
        addSelectedTags(key);
        push('/article/1');
    }

    function removeTags(key) {
        removeSelectedTags(key);
        push('/article/1');
    }

    function resetTags() {
        resetSelectedTags();
        push('/article/1');
    }
</script>

<div>
    <ul>
        {#each Object.entries($allTags) as [key, value]}
            {#if Object.keys($selectedTags).includes(key)}
                <li class="selected" on:click={removeTags(key)}>
                    <span class="hash">#</span>
                    <span class="key">{key}</span>
                    <span class="value">{value}</span>
                </li>
            {:else}
                <li on:click={addTags(key)}>
                    <span class="hash">#</span>
                    <span class="key">{key}</span>
                    <span class="value">{value}</span>
                </li>
            {/if}
        {/each}
        {#if Object.keys($selectedTags).length != 0}
                <li class="reset" on:click={resetTags}>
                    <span class="trash"><Trash width="12" height="12" /></span>
                    <span class="key">선택 해제</span>
                </li>
        {/if}
    </ul>
</div>

