<script>
    export let series;
    export let title;
    export let subtitle;

    import { onMount } from 'svelte';

    import { titleToUrl } from '../../../common/utils';
    import sticky from '../../../common/sticky';
    import { FullscreenFn, HistoryFn, RecentFn } from '../SeriesFunctions';

    import Layout from '../../Layout.svelte';
    import BottomNavigation from '../component/BottomNavigation.svelte';
    import FullScreenIcon from '../component/FullScreenIcon.svelte';
    import ToTopIcon from '../component/ToTopIcon.svelte';
    import ToBottomIcon from '../component/ToBottomIcon.svelte';

    let prev;
    let current;
    let next;

    let isStuck = false;
    let isFullscreen = FullscreenFn.store;

    onMount(() => {
        current = series.contents.filter(content => {
            return titleToUrl(content.title) === subtitle;
        })[0];
        prev = series.contents[parseInt(current.id) - 1];
        next = series.contents[parseInt(current.id) + 1];
        HistoryFn.setLatestPost(series.title, current.title);
        HistoryFn.setPostToRead(series.title, current.title);

        RecentFn.addRecent(series.title);
    });

    function handleStuck(e) {
        isStuck = e.detail.isStuck;
    }
</script>

<Layout>
    <div class="row row-1 {$isFullscreen ? 'full' : ''}">
        <div class="c">
            {#if current !== undefined}
                <div class="t">
                    {series.title} 
                </div>
                <div class="s">
                    <span class="i">{current.id + 1}. </span>{current.title}
                </div>
            {/if}
        </div>
    </div>
    <div class="row row-2">
        <div class="slot">
            <slot />
        </div>
        <div class="menu">
            <div>
                <FullScreenIcon />
                <ToTopIcon />
                <ToBottomIcon />
            </div>
        </div>
    </div>
    <div class="row row-3 {$isFullscreen ? 'full' : ''} {isStuck ? 'stuck' : ''}" use:sticky={false} on:stuck={handleStuck}>
        <BottomNavigation next={next} prev={prev} title={title} />
    </div>
</Layout>

